  position: relative;
}

.theme-switch__toggle {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1);
}

.theme-switch__marker {
  width: 18px;
  height: 18px;
  z-index: 2;
  pointer-events: none;
  transition: transform 250ms var(--timing-function);
  background-color: #fff;
  border-radius: 50%;
  position: relative;
  transform: translateX(1px);
  box-shadow: 1px 2px 4px rgba(0, 0, 0, .1);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background: linear-gradient(#4f2ee8, #686868);
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1);
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(21px);
}



